'use server';
/**
 * @fileOverview An AI agent for searching YouTube videos.
 *
 * - searchYouTubeVideos - A function that searches for YouTube videos based on a topic.
 * - SearchYouTubeVideosInput - The input type for the searchYouTubeVideos function.
 * - SearchYouTubeVideosOutput - The return type for the searchYouTubeVideos function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const YouTubeVideoSchema = z.object({
    title: z.string().describe('The title of the YouTube video.'),
    videoId: z.string().length(11).describe('The 11-character unique YouTube video ID.'),
    description: z.string().describe('A brief description of the video.'),
});
type YouTubeVideo = z.infer<typeof YouTubeVideoSchema>;

const SearchYouTubeVideosInputSchema = z.object({
  topic: z.string().describe('The topic to search for on YouTube.'),
});
export type SearchYouTubeVideosInput = z.infer<typeof SearchYouTubeVideosInputSchema>;

const SearchYouTubeVideosOutputSchema = z.object({
  videos: z.array(YouTubeVideoSchema).describe('A list of relevant YouTube videos.'),
});
export type SearchYouTubeVideosOutput = z.infer<typeof SearchYouTubeVideosOutputSchema>;

export async function searchYouTubeVideos(input: SearchYouTubeVideosInput): Promise<SearchYouTubeVideosOutput> {
  return searchYouTubeVideosFlow(input);
}

const prompt = ai.definePrompt({
  name: 'searchYouTubeVideosPrompt',
  input: {schema: SearchYouTubeVideosInputSchema},
  output: {schema: SearchYouTubeVideosOutputSchema},
  prompt: `You are an expert at finding relevant YouTube videos. 
  
  Based on the topic below, find the 5 most relevant and helpful videos. For each video, provide its title, a brief description, and its 11-character YouTube video ID.
  
  Topic: {{{topic}}}
  
  Respond with a JSON object containing a 'videos' array. Each object in the array must have 'title', 'description', and 'videoId' string properties. Ensure the videoId is exactly 11 characters long.`,
});

const searchYouTubeVideosFlow = ai.defineFlow(
  {
    name: 'searchYouTubeVideosFlow',
    inputSchema: SearchYouTubeVideosInputSchema,
    outputSchema: SearchYouTubeVideosOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
