"use client";

import { useState, useTransition } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
  BookText,
  Expand,
  HelpCircle,
  Loader2,
  PenSquare,
  Wrench,
  Calendar,
  Youtube,
  Plus,
  Trash2,
} from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

type ActionType = 'WHAT' | 'HOW' | 'WHEN' | 'EXPLAIN' | 'EXPAND' | 'CUSTOM' | 'YOUTUBE' | 'DELETE';

interface ToolboxProps {
  isNodeSelected: boolean;
  onAction: (actionType: ActionType, data?: string) => Promise<void>;
}

const actionButtons = [
  { type: 'WHAT', label: 'What', icon: HelpCircle },
  { type: 'HOW', label: 'How', icon: Wrench },
  { type: 'WHEN', label: 'When', icon: Calendar },
  { type: 'EXPLAIN', label: 'Explain', icon: BookText },
  { type: 'EXPAND', label: 'Expand', icon: Expand },
  { type: 'YOUTUBE', label: 'YouTube', icon: Youtube },
];

export function Toolbox({ isNodeSelected, onAction }: ToolboxProps) {
  const [isPending, startTransition] = useTransition();
  const [isCustomInputVisible, setCustomInputVisible] = useState(false);
  const [customQuery, setCustomQuery] = useState('');
  const { toast } = useToast();

  const handleActionClick = (actionType: ActionType) => {
    if (!isNodeSelected) {
      toast({ title: "Please select a node first.", variant: "destructive" });
      return;
    }
    startTransition(async () => {
      await onAction(actionType);
    });
  };

  const handleDeleteClick = () => {
    if (!isNodeSelected) {
      toast({ title: "Please select a node to delete.", variant: "destructive" });
      return;
    }
    startTransition(async () => {
      await onAction('DELETE');
    });
  };

  const handleCustomQuerySubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!customQuery.trim() || !isNodeSelected) return;
    
    startTransition(async () => {
      await onAction('CUSTOM', customQuery);
      setCustomQuery('');
      setCustomInputVisible(false);
    });
  };

  return (
    <Card className="absolute top-4 right-4 z-10 w-80 shadow-lg bg-card/80 backdrop-blur-sm">
      <CardContent className="p-4 space-y-4">
        <div className="grid grid-cols-3 gap-2">
          {actionButtons.map(({ type, label, icon: Icon }) => (
            <Button
              key={type}
              variant='outline'
              onClick={() => handleActionClick(type as ActionType)}
              disabled={isPending || !isNodeSelected}
              className="flex flex-col h-20"
            >
              <Icon className="w-6 h-6 mb-1" />
              <span>{label}</span>
            </Button>
          ))}
        </div>

        <div className="space-y-2">
            <Button 
              variant={isCustomInputVisible ? 'default' : 'outline'}
              onClick={() => setCustomInputVisible(prev => !prev)}
              disabled={isPending || !isNodeSelected}
              className="w-full justify-start"
            >
              <PenSquare className="mr-2 h-5 w-5" />
              Enter Yours
            </Button>
            
            {isCustomInputVisible && (
              <form onSubmit={handleCustomQuerySubmit} className="space-y-2 p-2 border rounded-md">
                 <Label htmlFor="custom-query">Your custom question</Label>
                 <Input
                    id="custom-query"
                    value={customQuery}
                    onChange={(e) => setCustomQuery(e.target.value)}
                    placeholder="e.g., How does it relate to aging?"
                    disabled={isPending}
                 />
                 <Button type="submit" disabled={isPending || !customQuery.trim()} className="w-full">
                    {isPending ? <Loader2 className="animate-spin" /> : <><Plus className="mr-2 h-4 w-4" /> Add Node</>}
                 </Button>
              </form>
            )}
        </div>
        
        <Button
          variant="destructive"
          onClick={handleDeleteClick}
          disabled={isPending || !isNodeSelected}
          className="w-full"
        >
          <Trash2 className="mr-2 h-5 w-5" />
          Delete Selected Node
        </Button>

        {isPending && <div className="absolute inset-0 bg-background/50 flex items-center justify-center"><Loader2 className="animate-spin h-8 w-8 text-primary"/></div>}
      </CardContent>
    </Card>
  );
}
